import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'
import Layout from '../components/layout/layout'
import SEO from '../components/seo/seo'
import BlogEntry from '../components/blog/blog-entry/blog-entry'

const BlogEntryTemplate = ({ data }) => {
  const { title, content, author, date } = data.data
  return (
    <Layout>
      <SEO title={title} />
      <BlogEntry
        title={title}
        content={content}
        authorId={author}
        date={date}
      />
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    data: wordpressPost(slug: { eq: $slug }) {
      id
      date(formatString: "MM/DD/YYYY")
      link
      title
      path
      slug
      author
      content
      categories {
        name
        slug
      }
    }
  }
`

BlogEntryTemplate.propTypes = {
  data: PropTypes.object.isRequired,
}

export default BlogEntryTemplate
