import React from 'react'
import PropTypes from 'prop-types'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import Button from '@material-ui/core/Button'
import SectionHeader from '../../layout/sections-header/section-header'
import withStyles from './blog-entry.style'
import BlogEntryMeta from './blog-entry-meta'

const BlogEntry = ({ classes, title, content, authorId, date }) => (
  <div className={classes.root}>
    <span id="top" className={classes.offset} />
    <Card elevation={3} className={classes.content}>
      <CardHeader
        title={<SectionHeader title={title} align={'left'} />}
        subheader={<BlogEntryMeta id={authorId} date={date} />}
      />
      <CardContent dangerouslySetInnerHTML={{ __html: content }} />
    </Card>
    <div className={classes.button}>
      <Button
        variant={'contained'}
        size={'large'}
        color={'primary'}
        className={classes.buttonText}
        href={'mailto:hello@compose.us?subject=Servus'}
      >
        Ich möchte mehr wissen!
      </Button>
    </div>
  </div>
)

BlogEntry.propTypes = {
  classes: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  authorId: PropTypes.number.isRequired,
  date: PropTypes.string.isRequired,
}

export default withStyles(BlogEntry)
